import Vue from "vue"
import VueRouter from "vue-router"
import buy from "../pages/buy"
import find from "../pages/find"
import attent from "../pages/attent"
import service from "../pages/service"
import my from "../pages/my"
import product from "../pages/product"


Vue.use(VueRouter)


let router = new VueRouter({
    routes:[
        {
        path:"/",
        name:"buy",
        component:buy
        },
        {
        path:"/find",
        name:"find",
        component:find
        },
        {
        path:"/attent",
        name:"attent",
        component:attent
        },
        {
        path:"/service",
        name:"service",
        component:service
        },
        {
        path:"/my",
        name:"my",
        component:my
        },
        {
        path:"/product",
        name:"product",
        component:product
        }
    ]
})

export default router
